.internal {
  width: 100%;

  margin-top: 2vw;
}
.table1container,
.table2container {
  width: 100%;
  padding: 0.7vw;
  background: #ffffff;
  border: 0.5px solid rgba(18, 18, 18, 0.15);
  border-radius: 6px;
}
.table1 {
  width: 100%;
}
.table2container {
  margin-top: 1vw;
}
.table2 {
  width: 100%;
}

/* ---------------------------- */
.dattime {
  width: 20%;
}
.dattime2 {
  width: 20%;
}
.name {
  width: 20%;
}
.name2 {
  width: 20%;
}
.trans {
  width: 23%;
}
.trans2 {
  width: 25%;
}
.amount {
  width: 17%;
}
.amount2 {
  width: 17%;
}
.purpose {
  width: 20%;
}
.purpose2 {
  width: 18%;
}
