.dots {
  width: 2.8%;
  overflow: hidden !important;
}

.dattimee,
.id {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.dattimee p,
.id p {
  margin-bottom: 0.3vw !important;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 0.8vw;
  line-height: 15px;

  color: #121212;
}

.dattimee span,
.id span {
  font-weight: 600;
}

.details {
  display: flex;
  gap: 0.6vw;
  align-items: center;
}
.details img {
  height: 3vw;
  width: 3vw;
  border-radius: 50%;
}
.details .text {
  display: flex;
  flex-direction: column;
}
.details .email {
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 1vw;
  line-height: 19px;
  margin-bottom: 0.3vw !important;
  color: rgba(0, 0, 0, 0.9);
}

.ga {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 0.9vw;
  line-height: 19px;
  margin-bottom: 0 !important;
  color: rgba(0, 0, 0, 0.9);
}

.table2 tr {
  display: flex;
  align-items: center;
  align-items: center;
  height: 4vw;
}

tr td {
  display: flex;
  height: 100%;
}

.purposediv {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 0.9vw;
  line-height: 17px;
  /* identical to box height */

  letter-spacing: -0.01em;
  padding: 0.3vw;
  color: #121212;
  height: 100%;

  width: 100%;
  /* overflow: scroll !important; */

  text-overflow: ellipsis !important;
}

.purposediv::-webkit-scrollbar {
  width: 0;
  height: 0;
}

.dots img {
  height: 1.4vw;
  cursor: pointer;
}

.dotsdiv {
  display: grid;
  place-items: center;
  overflow: hidden !important;
}

.divider {
  height: 0.4px;
  background-color: #d3c5c5;
  width: 102%;
  margin-top: 0.5vw;
  margin-left: -0.8vw;
}

.table1 tr {
  height: 2.5vw !important;
}
